.datepicker-form {
	cursor: pointer;
}

.datepicker {
	.datepicker-switch {
		&:hover {
			background-color: transparent;
		}
	}

	&.datepicker-dropdown {
		overflow: initial;
	}
}

.datepicker-dropdown {
	background-color: $white;
	border: 0;
	padding: 0;
	border-radius: 0;
	box-shadow: 0 1px 10px rgba(0,0,0,0.2);

	table {
		margin: 0 auto;
		border-collapse: separate;
		border-spacing: 10px;

		tr {
			th {
				padding: 0;
				height: 27px;
				width: 27px;
				border-radius: 100%;
			}

			td {
				padding: 0;
				height: 27px;
				width: 27px;
				border-radius: 100%;

				span {
					height: 50px;
					line-height: 50px;
					border-radius: 100%;

					&.active {
						background: $gold!important;
						color: $white;
						border: 0;
						box-shadow: none;

						&.active {
							background: $gold!important;
							color: $white;
						}

						&:hover {
							&.active {
								background: $gold!important;
								color: $white;	
							}
						}
					}
				}

				.decade {
				}

				&.today {
					background: $gold!important;
					color: $white;

					&:hover {
						&:hover {
							background: $gold!important;
							color: $white;	
						}
					}
				}

				&.active {
					background: $green!important;
					color: $white;
					border: 0;
					box-shadow: none;

					&.active {
						background: $green!important;
						color: $white;
					}

					&:hover {
						&.active {
							background: $green!important;
							color: $white;	
						}
					}
				}
			}
		}

		thead {
			th {
				i {
					font-size: 1.1em;
					line-height: 100%;
					pointer-events: none;
				}
			}
		}

		tfoot {
			display: none;
		}
	}

	&.date-sort {
		padding: 58px 20px 20px;
	}
}